footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: white;
  background: rgb(79, 87, 84);
  padding: 10px;
  width: 100%;

  div {
    display: flex;
    flex-direction: row;

    p {
      a {
        text-decoration: none;
        color: white;
        padding: 10px;
      }

      &:hover {
        transform: scale(1.3, 1.3);
        text-shadow: 2px 2px 10px gray;
      }
    }
  }
}