.Skills {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;

  .skillBlock {
    width: 49%;
    box-shadow: 1px 1px 5px gray;
    border-radius: 5px;
    background: linear-gradient(45deg, rgb(186, 232, 238), rgb(223, 239, 241));
  }

  h5 {
    padding-left: 20px;
    font-weight: 900;
  }
}